import React from "react";
import "../Style/name.css";
import "../Style/socialMedia.css";
import {
    MDBCol,
    MDBContainer,
    MDBRow,
    MDBFooter,
    MDBIcon,
    MDBBtn,
} from "mdbreact";

const FooterPage = () => {
    return (
        <div>
            <MDBFooter color="blue" className="font-small pt-4 mt-4">
                <MDBContainer fluid className="text-center text-md-left">
                    <p className="bigItalicsCentre">
                        Thank You For Visiting My Webiste!
                    </p>
                    <p className="smallerItalicsCentre">Please also visit:</p>
                    <div class="centered">
                        <MDBBtn size="lg" class="btn-linkedin">
                            <MDBIcon fab icon="linkedin" size="4x" />
                        </MDBBtn>
                        <MDBBtn size="lg" class="btn-linkedin">
                            <MDBIcon fab icon="github-square" size="4x" />
                        </MDBBtn>
                    </div>
                </MDBContainer>
                <div className="footer-copyright text-center py-3">
                    <MDBContainer fluid>
                        {new Date().getFullYear()} Copyright:
                        <p> Wisniowt.github.io/my-website-resume/ </p>
                    </MDBContainer>
                </div>
            </MDBFooter>
        </div>
    );
};

export default FooterPage;
