import React from "react";
import "../Style/Navbar.css";
import {
    MDBContainer,
    MDBCard,
    MDBCardImage,
    MDBCardBody,
    MDBCardTitle,
    MDBCardText,
    MDBBtn,
} from "mdbreact";
import ReactCardCarousel from "react-card-carousel";

export default class MyCarousel extends React.Component {
    static get CONTAINER_STYLE() {
        return {
            position: "relative",
            height: "50vh",
            width: "100%",
            display: "flex",
            flex: 1,
            justifyContent: "center",
            alignItems: "middle",
        };
    }

    static get CARD_STYLE() {
        return {
            height: "400px",
            width: "400px",
            textAlign: "center",
            background: "#52C0F5",
            color: "#FFF",
            fontFamily: "sans-serif",
            fontSize: "12px",
            textTransform: "uppercase",
            borderRadius: "10px",
            boxSizing: "border-box",
        };
    }

    render() {
        return (
            <div id="ProjectsSection">
                <MDBContainer className="mt-5 text-center" fluid>
                    <p className="biggerItalics">Projects</p>
                </MDBContainer>
                <div style={MyCarousel.CONTAINER_STYLE}>
                    <ReactCardCarousel autoplay={true} autoplay_speed={2500}>
                        <div style={MyCarousel.CARD_STYLE}>
                            <MDBCard>
                                <MDBCardImage
                                    className="img-fluid"
                                    src="https://mdbootstrap.com/img/Mockups/Lightbox/Thumbnail/img%20(67).jpg"
                                />
                                <MDBCardBody>
                                    <MDBCardTitle>Card title</MDBCardTitle>
                                    <MDBCardText>ONE</MDBCardText>
                                    <MDBBtn href="https://www.google.com/">
                                        Click
                                    </MDBBtn>
                                </MDBCardBody>
                            </MDBCard>
                        </div>
                        <div style={MyCarousel.CARD_STYLE}>
                            <MDBCard>
                                <MDBCardImage
                                    className="img-fluid"
                                    src="https://mdbootstrap.com/img/Mockups/Lightbox/Thumbnail/img%20(67).jpg"
                                />
                                <MDBCardBody>
                                    <MDBCardTitle>Card title</MDBCardTitle>
                                    <MDBCardText>TWO</MDBCardText>
                                    <MDBBtn href="https://www.google.com/">
                                        Click
                                    </MDBBtn>
                                </MDBCardBody>
                            </MDBCard>
                        </div>
                        <div style={MyCarousel.CARD_STYLE}>
                            <MDBCard>
                                <MDBCardImage
                                    className="img-fluid"
                                    src="https://mdbootstrap.com/img/Mockups/Lightbox/Thumbnail/img%20(67).jpg"
                                />
                                <MDBCardBody>
                                    <MDBCardTitle>Card title</MDBCardTitle>
                                    <MDBCardText>THREE</MDBCardText>
                                    <MDBBtn href="https://www.google.com/">
                                        Click
                                    </MDBBtn>
                                </MDBCardBody>
                            </MDBCard>
                        </div>
                        <div style={MyCarousel.CARD_STYLE}>
                            <MDBCard>
                                <MDBCardImage
                                    className="img-fluid"
                                    src="https://mdbootstrap.com/img/Mockups/Lightbox/Thumbnail/img%20(67).jpg"
                                />
                                <MDBCardBody>
                                    <MDBCardTitle>Card title</MDBCardTitle>
                                    <MDBCardText>FOUR</MDBCardText>
                                    <MDBBtn href="https://www.google.com/">
                                        Click
                                    </MDBBtn>
                                </MDBCardBody>
                            </MDBCard>
                        </div>
                        <div style={MyCarousel.CARD_STYLE}>
                            <MDBCard>
                                <MDBCardImage
                                    className="img-fluid"
                                    src="https://mdbootstrap.com/img/Mockups/Lightbox/Thumbnail/img%20(67).jpg"
                                />
                                <MDBCardBody>
                                    <MDBCardTitle>Card title</MDBCardTitle>
                                    <MDBCardText>FIVE</MDBCardText>
                                    <MDBBtn href="https://www.google.com/">
                                        Click
                                    </MDBBtn>
                                </MDBCardBody>
                            </MDBCard>
                        </div>
                    </ReactCardCarousel>
                </div>
            </div>
        );
    }
}
