import React from "react";
import "../Style/Navbar.css";
import {
    MDBJumbotron,
    MDBContainer,
    MDBRow,
    MDBCol,
    MDBCardImage,
    MDBCardBody,
    MDBCardText,
} from "mdbreact";
export default class Interests extends React.Component {
    render() {
        return (
            <div id="InterestsSection">
                <MDBContainer className="mt-5 text-center" fluid>
                    <p className="biggerItalics">Interests</p>

                    <MDBJumbotron className="text-center">
                        <MDBRow>
                            <MDBCol md="6">
                                <MDBCardImage className="img-fluid" />
                            </MDBCol>

                            <MDBCol md="6">
                                <MDBCardBody>
                                    <MDBCardText>Class!</MDBCardText>

                                    <MDBCol
                                        className="d-flex justify-content-center mt-4"
                                        md="12"
                                    >
                                        <MDBCol
                                            md="3"
                                            className="d-flex justify-content-around"
                                        ></MDBCol>
                                    </MDBCol>
                                </MDBCardBody>
                            </MDBCol>
                        </MDBRow>
                    </MDBJumbotron>
                </MDBContainer>
            </div>
        );
    }
}
