import React from "react";
import "../Style/Navbar.css";
import {
    MDBJumbotron,
    MDBContainer,
    MDBRow,
    MDBCol,
    MDBCardTitle,
    MDBCardImage,
    MDBCardBody,
    MDBCardText,
} from "mdbreact";

export default class Experiance extends React.Component {
    render() {
        return (
            <div id="ExperianceSection">
                <MDBContainer className="mt-5 text-center" fluid>
                    <p className="biggerItalics">Experience</p>

                    <MDBJumbotron className="text-center">
                        <MDBCardTitle className="card-title h4 pb-2">
                            <p className="bigBlackItalics">IBM Ireland</p>
                        </MDBCardTitle>
                        <MDBRow>
                            <MDBCol md="6">
                                <MDBCardImage
                                    src="https://clongowes.net/wp-content/uploads/2016/02/Welcome-Clongowes-Wood.jpg"
                                    className="img-fluid"
                                />
                            </MDBCol>

                            <MDBCol md="6">
                                <MDBCardBody>
                                    <MDBCardText>Class!</MDBCardText>

                                    <MDBCol
                                        className="d-flex justify-content-center mt-4"
                                        md="12"
                                    >
                                        <MDBCol
                                            md="3"
                                            className="d-flex justify-content-around"
                                        ></MDBCol>
                                    </MDBCol>
                                </MDBCardBody>
                            </MDBCol>
                        </MDBRow>
                    </MDBJumbotron>
                </MDBContainer>
            </div>
        );
    }
}
