import React from "react";
import "../Style/Navbar.css";
import "../Style/name.css";
import "../Style/card.css";
import {
    MDBJumbotron,
    MDBContainer,
    MDBRow,
    MDBCol,
    MDBCardTitle,
    MDBCardImage,
    MDBCardBody,
    MDBCardText,
} from "mdbreact";
import { CircleProgress } from "react-gradient-progress";

export default class Skills extends React.Component {
    render() {
        return (
            <div id="SkillsSection">
                <div className="smallPageDivide"></div>
                <MDBContainer fluid>
                    <p className="biggerItalicsCentre">
                        Skills &#38; Characteristic
                    </p>
                    <MDBRow>
                        <MDBCol md="6">
                            <MDBJumbotron>
                                <MDBCardTitle className="card-title h4 pb-2">
                                    <p className="bigBlackItalicsCentre">
                                        Skills
                                    </p>
                                </MDBCardTitle>
                                <MDBRow>
                                    <MDBCol md="4">
                                        <div className="indentedBox">
                                            <p className="smallBlackItalicsCentre">
                                                JavaScript
                                            </p>
                                            <CircleProgress
                                                percentage={90}
                                                strokeWidth={8}
                                                fontColor="#000000"
                                                secondaryColor="#f0f0f0"
                                            />
                                        </div>
                                    </MDBCol>
                                    <MDBCol md="4">
                                        <div className="indentedBox">
                                            <p className="smallBlackItalicsCentre">
                                                Python
                                            </p>
                                            <CircleProgress
                                                percentage={90}
                                                strokeWidth={8}
                                                fontColor="#000000"
                                                secondaryColor="#f0f0f0"
                                            />
                                        </div>
                                    </MDBCol>
                                    <MDBCol md="4">
                                        <div className="indentedBox">
                                            <p className="smallBlackItalicsCentre">
                                                Java
                                            </p>
                                            <CircleProgress
                                                percentage={90}
                                                strokeWidth={8}
                                                fontColor="#000000"
                                                secondaryColor="#f0f0f0"
                                            />
                                        </div>
                                    </MDBCol>
                                </MDBRow>
                                <div className="smallerPageDivide"></div>
                                <MDBRow>
                                    <MDBCol md="4">
                                        <div className="indentedBox">
                                            <p className="smallBlackItalicsCentre">
                                                Node.js
                                            </p>
                                            <CircleProgress
                                                percentage={90}
                                                strokeWidth={8}
                                                fontColor="#000000"
                                                secondaryColor="#f0f0f0"
                                            />
                                        </div>
                                    </MDBCol>
                                    <MDBCol md="4">
                                        <div className="indentedBox">
                                            <p className="smallBlackItalicsCentre">
                                                React.js
                                            </p>
                                            <CircleProgress
                                                percentage={90}
                                                strokeWidth={8}
                                                fontColor="#000000"
                                                secondaryColor="#f0f0f0"
                                            />
                                        </div>
                                    </MDBCol>
                                    <MDBCol md="4">
                                        <div className="indentedBox">
                                            <p className="smallBlackItalicsCentre">
                                                Java
                                            </p>
                                            <CircleProgress
                                                percentage={90}
                                                strokeWidth={8}
                                                fontColor="#000000"
                                                secondaryColor="#f0f0f0"
                                            />
                                        </div>
                                    </MDBCol>
                                </MDBRow>
                            </MDBJumbotron>
                        </MDBCol>
                        <MDBCol md="6">
                            <MDBJumbotron className="text-center">
                                <MDBCardTitle className="card-title h4 pb-2">
                                    <p className="bigBlackItalics">
                                        Characteristic
                                    </p>
                                </MDBCardTitle>
                                <MDBRow>
                                    <MDBCardBody>
                                        <MDBCardText>Torture!</MDBCardText>

                                        <MDBCol
                                            className="d-flex justify-content-center mt-4"
                                            md="12"
                                        >
                                            <MDBCol
                                                md="3"
                                                className="d-flex justify-content-around"
                                            ></MDBCol>
                                        </MDBCol>
                                    </MDBCardBody>
                                </MDBRow>
                            </MDBJumbotron>
                        </MDBCol>
                    </MDBRow>
                </MDBContainer>
            </div>
        );
    }
}
